.section-contact {
  position: relative; }
  .section-contact .copy {
    padding: 20px; }
    .section-contact .copy h2, .section-contact .copy p {
      padding-left: 24px;
      line-height: 1; }
  .section-contact .social {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center; }
    @media screen and (max-width: 580px) {
